  this.correctionDetailleeDisponible = false // bool√©en qui indique si une correction d√©taill√©e est disponible.
  this.correctionDetaillee = true // bool√©en indiquant si la correction d√©taill√©e doit √™tre affich√© par d√©faut (r√©cup√©r√© dans l'url avec le param√®tre `,cd=`).
  this.video = '' // Chaine de caract√®re pour un compl√©ment num√©rique (id Youtube, url, code iframe...).
